header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    background-color: var(--mainColor);
    color: var(--textColor);
  
  }
  
  nav Link{
    margin: 0 2rem;
    color: var(--textColor) ;
    text-decoration: none;
  }
  
  nav a:hover{
    color: var(--textColor);
  }
  
  header .nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
  }
  
  @media only screen and (max-width: 720px) {
       header .nav-btn{
        visibility: visible;
        opacity: 1;
       }
  
       header nav{
          position: fixed;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          gap: 1.5rem;
          background-color: var(--mainColor);
          transition: 1.5s;
          transform: translateY(-100vh);
       }
  
       header .responsive_nav{
          transform: none;
       }
  
       nav .nav-close-btn{
        position: absolute;
        top: 2rem;
        right: 2rem;
       }
       nav Link{
        font-size: 1.5rem;
       }
  
       
  }